.toggle {
  display: block;
  width: 24px;
  height: 18px;
  padding: 0;
  margin-left: 10px;
  position: relative;
  border: none;
  background-color: inherit;
  z-index: 2;
  cursor: pointer;
}
.toggle::before,
.toggle::after {
  content: "";
  position: absolute;
  top: 8px;
  left: 0;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: none;
}
.toggle::before {
  transform: rotate(45deg);
}
.toggle::after {
  transform: rotate(-45deg);
}
.toggle--closed {
  width: 24px;
  height: 16px;
}
.toggle--closed::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  transform: none;
}
.toggle--closed::after {
  transform: none;
}
@media (min-width: 768px) {
  .toggle--closed {
    display: none;
  }
}
.toggle--nojs {
  display: none;
}
@media (min-width: 768px) {
  .toggle {
    display: none;
  }
}
